
import axios, { AxiosRequestConfig } from "axios";
import cheerio from "cheerio";
import "@toast-ui/editor/dist/toastui-editor.css";
import "@toast-ui/editor/dist/toastui-editor-viewer";
import "@toast-ui/editor/dist/toastui-editor-viewer.css";
import Viewer from "@toast-ui/editor/dist/toastui-editor-viewer";

export default {
  created() {
    console.log("Created work");
    this.getPost();
    var author = JSON.parse(localStorage.getItem("profile") || "");
    this.author_id = author.id;
    this.getComment();
  },
  data: (): { post: any; viewer: any,tags :any[],author : any, catagory_name :any , catagory_id :any ,Comments : any[],comment_idEdit :any,comment:any,author_id:any,showLess : boolean} => ({
    post: null,
    viewer: null,
    tags : [],
    author : null,
    catagory_name : null,
    catagory_id : null,
    Comments :[],
    comment_idEdit :"",
    comment :"",
    author_id :"",
    showLess : false
  }),
  methods: {
   async getPost(){
      var id = this.$route.params.id;
      // var id = "1111111111111111"
      let apiUrl = "https://interm-api.onrender.com/api/post/getdetail/";
      await axios
        .get(apiUrl + id)
        .then((res) => {
          // this.post = res.data;
           console.log(res.data);
          this.tags = res.data.tbl_tags;
          this.author = res.data.author.name;
          this.catagory_name = res.data.catagory.name;
          this.catagory_id = res.data.catagory._id;
          this.viewer = new   Viewer({
            el: document.querySelector("#viewer")!,
            initialValue: res.data.content,
          });
          
        })
        .catch((err) => {
          console.log(err.response.data.message);
          alert(err.response.data.message);
        });
    },
    async getComment(){
      var post_id = this.$route.params.id;
      let apiUrl = "https://interm-api.onrender.com/api/post/getcommentbyid";
     await  axios.post(apiUrl,{post_id})
      .then((res) => {
          this.comment ="";
          this.comment_idEdit = "";
          this.Comments = res.data; 
        })
        .catch((err) => {
          console.log(err.response.data.message);
          alert(err.response.data.message);
        });
    },
    OnComment(){
      console.log('OnComment Working');   
      var text = this.comment;
      if(text){     
        var author = JSON.parse(localStorage.getItem("profile") || "");
        var author_id = author.id;
        const post_id = this.$route.params.id;
        
        let apiUrl = "https://interm-api.onrender.com/api/comments/create";
        axios.post(apiUrl,{text,author_id})
        .then((res) => {
          let comment_id = res.data.id;
          console.log(comment_id);
          console.log(res.data);
          axios.put("https://interm-api.onrender.com/api/post/addcomments",{post_id,comment_id})
          .then((res) =>{
            console.log(res.data);
            this.getComment();
          })
        })
        .catch((err) =>{
          console.log(err.response.data.message);
          alert(err.response.data.message);
        })
      }

    },
    OncommentsDelete(id : string){
      var comment_id = id;  
      let apiUrl ="https://interm-api.onrender.com/api/comments/delete/"
      axios.delete(apiUrl+comment_id)
      .then((res) =>{
        console.log(res); 
        const post_id = this.$route.params.id;
        const requestConfig: AxiosRequestConfig = {};
        requestConfig.data = { post_id: post_id,comment_id:comment_id };
        let apiUrl ="https://interm-api.onrender.com/api/post/deletecomment"
        axios.delete(apiUrl,requestConfig)
        .then((res) =>{
          this.getComment();
        })
        .catch((err) =>{
          console.log(err.response.data.message);
          alert(err.response.data.message);
        })
      })
      .catch((err) =>{
        console.log(err.response.data.message);
        alert(err.response.data.message);
      })
    },
    OnClickEdit(id : string){
      if (id) {
        this.comment_idEdit = id;
        this.$nextTick(() => {
          if (this.$refs["comments" + id]) {
            this.$refs["comments" + id][0].focus();
          }
        });
      } else {
        this.comment_idEdit = null;
      }
    },
    OncliclSaveEdit(text : string,id :string){
      console.log(text);
      let apiUrl = "https://interm-api.onrender.com/api/comments/update"
      axios.put(apiUrl,{id,text})
      .then((res) =>{
        this.getComment();
      })
      .catch((err) =>{
        console.log(err.response.data.message);
        alert(err.response.data.message);
      })
    }
  },
};
