
import axios from "axios";
import { defineComponent } from "vue";
import cheerio from 'cheerio';
export default defineComponent({
  created() {
    this.getallPost();
    this.getallTags();
    this.getTags()
  },
  data: (): {
    post: any[];
    tag: any;
    search :any
    tagsList:any[]
    showLess :boolean
  } => ({
    post: [],
    tag: null,
    tagsList:[],
    search:"",
    showLess : false
  }),
  computed: { 
      filteredList(){ 
      if(this.search){
          return this.tagsList.filter((tagList: { name: string; }) => {
            return tagList.name.toLowerCase().includes(this.search.toLowerCase())
        })
      }else{
        return null
      }
   },
   
  },  
  methods: {
    async getallPost() {
      this.$store.commit('UPDATE_LOADING',true)
      let apiUrl = "https://interm-api.onrender.com/api/post/getall";
      await axios
        .get(apiUrl)
        .then((res) => {
          this.post = res.data;
        })
        .catch((err) => { 
          console.log(err);
        })
        .finally(() =>  {
          setTimeout(() => {
             this.$store.commit('UPDATE_LOADING',false)
          },1000)
        })
    },
    async getTags() {
      let apiUrl = "https://interm-api.onrender.com/api/tags/tag";
      await axios
        .get(apiUrl, {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("access_token"),
          },
        })
        .then((res) => {
          this.tag = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    async getallTags() {
      let apiUrl = "https://interm-api.onrender.com/api/tags/getall";
      await axios
        .get(apiUrl, {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("access_token"),
          },
        })
        .then((res) => {
          this.tagsList = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    extractH1(htmlString : string) {
      const $ = cheerio.load(htmlString);    
      const h1 = $('h1').text();
      if(h1 != ""){
        return h1;
      }else{
         return "Unknown Title"
      }
    }, 
  }
});
