
import { defineComponent, inject, reactive } from "vue";
import { ILogin } from "@/interface/user";
import axios from "axios";
import router from "@/router";
import { getAuth, signInWithPopup, GoogleAuthProvider } from "firebase/auth";

export default defineComponent({
  methods: {
    async onclickLogin() {
      this.$store.commit("UPDATE_LOADING", true);
      let apiUrl = "https://interm-api.onrender.com/api/profile/login";
      await axios
        .post(apiUrl, this.states)
        .then((response) => {
          let profile = response.data.userInfo;
          let accessToken = response.data.accessToken;

          localStorage.setItem("profile", JSON.stringify(profile));
          localStorage.setItem("access_token", accessToken);

          router.push("/user/home");
        })
        .catch((error) => {
          console.log(error.response.data);
          alert(error.response.data.message);

          this.$store.commit("UPDATE_LOADING", false);
        });
    },
    onclickGoogleLogin(){
      const auth = getAuth();
      const provider = new GoogleAuthProvider();
      signInWithPopup(auth, provider)
        .then(async(result) => {
          const email = result.user.email;
          const provider_id = result.user.uid;

          let apiUrl = "https://interm-api.onrender.com/api/profile/loginwithgoogle";
            await axios.post(apiUrl,{email,provider_id})
              .then((response) => {
              let profile = response.data.userInfo;
              let accessToken = response.data.accessToken;

              localStorage.setItem("profile", JSON.stringify(profile));
              localStorage.setItem("access_token", accessToken);

              router.push("/user/home");
        })
        .catch((error) => {
          console.log(error.response.data);
          alert(error.response.data.message);
          this.$store.commit("UPDATE_LOADING", false);
        });   
        }).catch((error) => {
          // Handle Errors here.
          const errorCode = error.code;
          const errorMessage = error.message;
          console.log(errorCode);
          console.log(errorMessage);
        });
    }
  },
  setup() {
    const states = reactive<ILogin>({
      email: "",
      password: "",
    });
    const firebaseAuth: any = inject("firebaseAuth");
    return {
      states,firebaseAuth,
    };
  },
});
